<template>
  <div class="container">
    <div class="container__slider">
      <el-carousel height="350px" autoplay>
        <el-carousel-item v-for="item in MENUS" :key="item.src">
          <img
            @click="handleClickImg(item.path)"
            :src="item.src"
            :alt="item.name + '-img'"
            style="cursor: pointer"
          />
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="container__bread">
      <div class="container__bread__content">
        当前位置：
        <el-breadcrumb separator=">">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/news' }"
            >新闻及展会</el-breadcrumb-item
          >
          <el-breadcrumb-item>{{ route.query.name }}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </div>
    <div class="container__content">
      <el-menu
        default-active="/news?name=新闻"
        class="container__content__menu"
        @open="handleOpen"
        router
      >
        <el-submenu index="1">
          <template #title>
            <span>新闻及展会</span>
          </template>
          <el-menu-item-group>
            <el-menu-item index="/news?name=新闻">新闻</el-menu-item>
            <el-menu-item index="/news?name=展会">展会</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
      <div class="container__solutions">
        <div class="container__solutions__title">新闻及展会</div>
        <div class="container__solutions__content"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { useRoute } from 'vue-router'
import { useRouter } from 'vue-router'
export default {
  components: {},

  setup() {
    const route = useRoute()
    const router = useRouter()
    const MENUS = [
      {
        name: 'products',
        src: 'http://www.gppv-energy.com/Templates/cn/images/01.jpg',
        path: '/products?name=166九栅半片组件'
      },
      {
        name: 'about',
        src: 'http://www.gppv-energy.com/Templates/cn/images/02.jpg',
        path: '/about?name=企业简介'
      }
    ]
    const handleClickImg = path => {
      router.push(path)
    }
    const handleOpen = (key, keyPath) => {
      console.log(key, keyPath)
    }
    return {
      MENUS,
      route,
      handleClickImg,
      handleOpen
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  width: 100%;
  &__slider {
    max-width: 100%;
    height: 350px;
    text-align: center;
  }
  &__bread {
    max-width: 100%;
    height: 50px;
    border-bottom: 1px solid #efefef;
    background: #f8f8f8;
    display: flex;
    align-items: center;
    &__content {
      width: 1200px;
      margin: 0 auto;
      display: flex;
      flex-direction: row;
      font-size: 14px;
    }
  }
  &__content {
    width: 1200px;
    height: auto;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    /deep/ .el-menu {
      border-right: transparent;
    }
    &__menu {
      width: 275px;
      height: auto;
    }
  }
  &__solutions {
    margin-left: 10px;
    width: calc(100% - 285px);
    min-height: 380px;
    &__title {
      width: 100%;
      height: 50px;
      line-height: 50px;
      text-indent: 15px;
      font-size: 16px;
      font-weight: bold;
      color: #db0818;
      background: #efeff4;
      opacity: 0.8;
    }
    &__content {
      padding: 20px;
      overflow-y: auto;
    }
  }
}
</style>
